import React from "react";
import Typed from "react-typed";
import { useSelector } from "react-redux";
import { setActiveIndex } from "../store/store";

const contactText = [
  "!   .      ,         .             ,     .                .        .",
];

export default function Contact(props) {
  const { activeIndex } = useSelector((state) => state);

  return (
    <div>
      <Typed
        strings={contactText}
        typeSpeed={40}
        backSpeed={50}
        attr="placeholder"
        smartBackspace
      >
        <textarea
          row="6"
          style={{
            background: "none",
            border: "none",
            color: "#fff",
            width: "100%",
            textAlign: "left",
            height: "250px",
            fontSize: "1rem",
          }}
        />
      </Typed>
    </div>
  );
}
